* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell,
    'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
}

.timer {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 40px;
}

.field {
  margin: 5px;
}
.value {
  font-size: 60px;
}
.label {
  font-size: 20px;
}
#datetime-picker {
  height: 35px;
  text-align: center;
  margin-right: 10px;
  font-size: 20px;
}
.chose-data-field {
  display: flex;
  justify-content: center;
}
.chose-data-field > button {
  font-size: 18px;
  letter-spacing: 2px;
}
